body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.data-container {
  min-height: 85vh;
  padding-top: 50px;
  padding-right: 80px;
  padding-left: 80px;
  background-color: beige;
}
.header {
  position: absolute;
  top: 0;
  z-index: 999
}
.footer-container{
  
}
.Product{
  position: relative;
  display: table;
  width: auto;
  margin: 0px;
}
.inliner{
  display: inline-block;
  margin: 10px;
}

img {
  border-radius: 8px;
  height: auto; 
  width: auto; 
  max-width: 300px; 
  max-height: 300px;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
  box-shadow: 1px 1px 5px grey;
}
.middle {
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  opacity: 0.3;
  -webkit-transition: .5s ease;
  transition: .5s ease;
  -webkit-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
}
img:hover {
  position: relative;
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}
.Product:hover .middle{
  opacity: 1;
}

.text {
  background-color: rgba(65, 71, 65, 0.466);
  border-radius: 8px;
  color: white;
  font-size: 16px;
  padding: 7px 32px;
}

.Sticker {
    padding-top: 50px;
    padding-left: 0px;
    padding-right: 0px;
    padding-bottom: 10px;
    background-color: #efbe43;
  }
.DMenu{
    position: absolute;
    right: 10px;
    top: 10px;
}
.Titulo{
    position: absolute;
    top: 50px;
    font-family: 'Raleway', sans-serif;
    font-size: 50px;
    font-style: italic;
  }
.Topping{
  top: 0;
  width: 100%;
  overflow: hidden;
}


